<template>
  <div class="Neologism">
    <h1>신조어</h1>
    <h2>식물 가꾸기 트렌드를 신조어로 알아보는 서비스입니다.:)</h2>
    <div v-for="n in neologism" :key="n">
      <div>
      {{n.neologism}}
      </div>
      <div>
      {{n.meaning}}
      </div>
    </div>
  </div>
</template>

<script>
export default {
	name: 'Neologism',
	data() {
		return {
			neologism: []
		}
	},
	methods: {
		test: function() {
		}
	},
	beforeCreate: function() {
		console.log('beforeCreate')

		this.$axios.get('/neologism')
			.then((res) => {
				let list = res.data

				for (var i in list) {
					this.neologism.push(list[i])
				}
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


